/* App.css */

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-image: url('../assets/background.jpg');
  background-size: cover;
  background-position: center;
}

.container {
  max-width: 100%;
  height: 100vh; /* Set the container height to 100vh to fill the entire viewport */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: black;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  padding: 20px;
}

img {
  max-width: 50%;
  height: auto;
  margin-bottom: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

h1 {
  margin-bottom: 10px;
  font-size: 32px; /* Increase font size for the title */
}

h2 {
  margin-bottom: 5px;
}

ul,
ol {
  margin-left: 20px;
}

li {
  margin-bottom: 5px;
}

p {
  margin-bottom: 20px;
}

.back-link {
  display: block;
  margin-top: 20px;
  color: #fff;
  text-decoration: none;
  border: 1px solid #fff;
  padding: 8px 16px;
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.5);
  text-align: center;
}

.back-link:hover {
  background-color: rgba(0, 0, 0, 0.7);
}
